@import url(https://fonts.googleapis.com/css?family=Sintony&display=swap);
@import url(https://fonts.googleapis.com/css?family=Palanquin&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
  color: inherit;
}

.login {
  position: absolute;
  top: 30%;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  background-color: white;
  height: 350px;
  width: 700px;
  border-radius: 15px;
}

.left-side {
  position: absolute;
  top: 0;
  height: inherit;
  width: 40%;
  display: flex;
  flex-direction: column;
  color: white;
  border-top-left-radius: 15px;
  border-bottom-left-radius: 15px;
  background: no-repeat url("/asset1.jpg");
  background-size: auto 120%;
  background-position: 62% 95%;
}

.left-container {
  position: absolute;
  top: 45px;
  left: 40px;
}

.title {
  text-transform: uppercase;
  width: 90%;
  font-weight: lighter;
  line-height: 25px;
}

.left-container > p {
  line-height: 5px;
}

.right-side {
  position: absolute;
  top: 0;
  width: 60%;
  left: 40%;
  height: inherit;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}

.logo {
  margin: 10px auto 10px 15%;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
}
.text-field {
    width: 300px;
    height: 20px;
}

h2 {
    color: grey;
    font-weight: lighter;
    margin-bottom: -3px;
}

.register-button {
    display: block;
    color: white;
    font-feature-settings: "c2sc";
    font-variant: small-caps;
    font-weight: bold;
    font-size:14px;
    background-color: #8B1F8D !important;
    border-radius: 50px;
    height: 25px;
    width: 90px;
    border: none;
}

button:hover {
    cursor: pointer;
    box-shadow: inset 0 0 100px 100px rgba(255, 255, 255, 0.1);
}

button:focus {
    outline: none;
}

.button-container {
    display: flex;
    justify-content: center;
    padding: 15px;
}

.oauth-container {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    width: 200px;
    height: 30px;
    text-decoration: none;
    box-shadow: 0 3px 6px -1px rgba(0, 0, 0, 0.3);
    border-radius: 5px;
    margin-bottom: 10px;
}

.google-signin {
    display: inline-block;
    margin: 0 40px;
    color: grey;
    font-size: 10px;
}
.app {
  height: 100%;
  overflow: hidden;
  position: relative;
  height: 100vh;
  background: no-repeat url("/asset2.png");
  background-position: center;
  background-attachment: fixed;
  background-size: cover;
  font-family: 'Sintony', sans-serif;
  font-size: 10px;
}

h1 {
  font-family: 'Palanquin', sans-serif;
  font-size: 22px;
}

h2 {
  font-size: 12px; 
}

@media only screen and (max-width: 700px) {
  .app {
    overflow: visible;
    overflow: initial;
  }
}
